  max-width: 1036px;
  width: 100%;
  background: hsl(249, 17%, 15%);
  border: 1px solid hsl(249, 14%, 27%);
  border-radius: 0.2rem;
  color: hsl(257, 4.3%, 68.4%);
}
.c4contesttile .tile--body {
  padding-top: 0.5rem;
  display: flex;
  flex-direction: column-reverse;
  gap: 0;
}
.c4contesttile .tile--body header {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.c4contesttile .tile--body a.tile--body--logo {
  max-width: 90px;
  max-height: 90px;
  border-radius: 50%;
  margin: auto;
}
.c4contesttile .tile--body a.tile--body--logo img {
  object-fit: cover;
  border: 1px solid hsl(249, 14%, 27%);
  border-radius: 50%;
  background: white;
  min-width: 88px;
  min-height: 88px;
  max-width: 88px;
  max-height: 88px;
}
.c4contesttile .tile--body img.tile--body--logo {
  object-fit: cover;
  border-radius: 50%;
  background: white;
  border: 1px solid hsl(249, 14%, 27%);
  min-width: 88px;
  min-height: 88px;
  max-width: 88px;
  max-height: 88px;
  margin: auto;
}
.c4contesttile .tile--body--botracestatus {
  display: inline-flex;
  gap: 0.5rem;
  align-items: center;
  color: hsl(0, 0%, 100%);
  font-weight: 700;
}
.c4contesttile .tile--body--period, .c4contesttile .tile--body--description {
  display: block;
  text-align: center;
}
.c4contesttile .tile--body--title {
  margin: 0rem;
  color: hsl(0, 0%, 100%);
  text-align: center;
}
.c4contesttile .tile--body--description {
  margin: 0.5rem 0rem 0rem;
}
.c4contesttile .tile--body--amount {
  margin: 0rem;
  border-bottom: 1px solid hsl(249, 14%, 27%);
  padding: 0rem 1rem 0.5rem;
  text-align: right;
  color: hsl(0, 0%, 100%);
  font-weight: 500px;
  font-size: 1.2rem;
}
@media (min-width: 450px) {
  .c4contesttile .tile--body header {
    flex-direction: row;
  }
  .c4contesttile .tile--body a.tile--body--logo, .c4contesttile .tile--body img.tile--body--logo {
    margin: unset;
  }
  .c4contesttile .tile--body--period, .c4contesttile .tile--body--description, .c4contesttile .tile--body--title {
    text-align: left;
  }
}
@media (min-width: 960px) {
  .c4contesttile .tile--body {
    padding: 1rem;
    flex-direction: row;
    gap: 2rem;
  }
  .c4contesttile .tile--body header {
    padding: 0rem;
  }
  .c4contesttile .tile--body--amount {
    border-bottom: none;
    padding: 0rem;
  }
}
.c4contesttile .tile--footer {
  border-top: 1px solid hsl(249, 14%, 27%);
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  height: fit-content;
}
.c4contesttile .tile--footer--details {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.c4contesttile .tile--footer--status, .c4contesttile .tile--footer--timer, .c4contesttile .tile--footer--contestredirect {
  padding: 0.5rem 1rem;
}
.c4contesttile .tile--footer--status {
  width: max-content;
  border-right: 1px solid hsl(249, 14%, 27%);
}
.c4contesttile .tile--footer--timer {
  width: 100%;
  margin: 0px;
}
.c4contesttile .tile--footer--contestredirect {
  width: calc(100% - 97px);
  border-top: 1px solid hsl(249, 14%, 27%);
  text-decoration: none;
  color: hsl(0, 0%, 100%);
}
.c4contesttile .tile--footer--contestredirect:hover {
  background: hsl(251, 15.7%, 20%);
}
.c4contesttile .tile--footer--dropdown {
  border-left: 1px solid hsl(249, 14%, 27%);
  border-top: 1px solid hsl(249, 14%, 27%);
}
.c4contesttile .tile--footer--dropdown button {
  padding: 0rem 1rem;
  height: 100%;
  background: none;
  border: none;
  color: hsl(0, 0%, 100%);
  cursor: pointer;
}
.c4contesttile .tile--footer--dropdown button img {
  height: 100%;
}
.c4contesttile .tile--footer--dropdown button:hover {
  background: hsl(251, 15.7%, 20%);
}
@media (min-width: 960px) {
  .c4contesttile .tile--footer {
    flex-wrap: nowrap;
  }
  .c4contesttile .tile--footer--timer {
    border-right: 1px solid hsl(249, 14%, 27%);
  }
  .c4contesttile .tile--footer--contestredirect {
    width: fit-content;
    white-space: nowrap;
    border-top: none;
  }
  .c4contesttile .tile--footer--dropdown {
    border-top: none;
  }
